@import url('https://fonts.googleapis.com/css?family=Lato:400&display=swap');
@import url('https://fonts.googleapis.com/css?family=Amatic+SC:400&display=swap');

.App{
  background: linear-gradient(150deg, #ffc837, #ff8008);
  min-height: 100vh;
  margin-top: 0;
}

h1{
  font-family: 'Amatic SC', cursive;
  font-weight:400;
  font-size:400%;
  text-align: center;
  margin-top: 0;
}

.intro{
  font-family: 'Lato', sans-serif;
  font-weight:400;
  text-align: center;
  font-size:120%;
  line-height: 1.5;
}

.burger{
  display: block;
  width: 30%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  min-width: 200px;
}

.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
  border-radius: 25px;
  font-family: 'Lato', sans-serif;
  font-weight:400;
  font-size:120%;

}

input:focus{
  outline: none;
}
input::placeholder {
  color: grey;
}

.search-button{
  background: #ff6308;
  font-family: 'Amatic SC', cursive;
  font-size:120%;
  border: none;
  margin-left: 15px;
  padding: 10px 20px;
  color: white;
  letter-spacing: 0.05em;
  border-radius: 25px;
}

.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

